.NavigationItem{
    margin: 10px 0;
    box-sizing: border-box;
}
.NavigationItem a{
    color: #8f5c2c;
    text-decoration: none;
    padding: 16px 10px;
    box-sizing: border-box;
    display: block;
}
.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    color: #40a4c8;
}
@media (min-width:500px) {
    .NavigationItem{
        margin: 0;
        display: flex;
        height: 100%;
        align-items: center;
    }
    .NavigationItem a{
        color: white;
        padding: 16px 10px;
        height: 100%;
        width: auto;
        border-bottom: 4px solid transparent;
    }
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active{
        background-color: #8f5c2c;
        border-bottom: 4px solid #40a4c8;
        color: white;
    }   
}